<template>
  <div class="main">
    <h3>Qualidade vs. Velocidade</h3><a href="#" v-scroll-to="'#introduction'" style="float:right">Voltar ao topo</a>

    <p><i>Postado em 29 de janeiro de 2021</i></p>

    <img
      alt="Vue logo"
      src="@/assets/3dprintweek/camadas01.jpg"
