    />
    <p>
      Com a orientao do objeto em relao  mesa de impresso definida, a
      altura de cada camada que ser impressa  fator chave na qualidade dos
      detalhes do objeto. Quando mais finas as camadas, mais detalhes podem ser
      capturados pelos movimentos da impressora, entretanto, mais camadas sero
      necessrias para atingir a altura do objeto, consequentemente, maior o
      tempo de impresso.
    </p>
    <p>
      Se qualidade no  um fator importante, por exemplo, para peas
      funcionais, pode-se usar camadas mais altas, para reduzir o tempo de
      impresso, considerando um limite mximo baseado no dimetro do bico de
      impresso (quando mais o dimetro, maior altura de camada  possvel
      atingir). Um cuidado que se deve tomar,  o equilibrio entre temperatura,
      altura da camada e a velocidade dos movimentos.
    </p>
    <p>
      Embora exista o risco de se perder qualidade nos detalhes, pode-se
      aumentar a velocidade de movimento dos motores que controlam as direes
      horizontais e verticais (X e Y) do hotend, mas com a necessidade de
      depositar (extrusar) plstico derretido de forma mais rpida,
      eventualmente a temperatura precisa ser calibrada. Esse equilbrio dos
      parmetros  o grande desafio da pessoa que quer imprimir algum objeto.
    </p>
    <p>
      As vezes, diferentes objetos, precisam de diferentes configuraes, mesmo
      utilizando o mesmo material e a prtica  a nica forma de atingir bons
      resultados. Quanto mais uma pessoa conhecer os limites da sua impressora,
      melhor sero os objetos impressos.
    </p>
