      para sada do plstico.
    </p>
    <p>
      Existe um motor que controla a vazo de plstico que passa por esse hotend
      denominado "extrusor". Outros motores so responsveis por controlar a
      posio horizontal (eixo X) e vertical (eixo Y) do bico em relao ao
      plano da mesa.
    </p>
    <p>
      Por fim, a altura do objeto  controlada pela quantidade de camadas de
      plstico que ser depositada e, conforme a impressora vai construindo as
      camadas, um outro conjunto de motores faz com que o bico v subindo (eixo
      Z).
    </p>
    <p>
      Curiosidade: Esse tipo de sistema de movimento da impressora  denominado
      "cartesiano", mas existem outros tipos de impressoras como as tipo Delta.
