    <p>
      Normalmente, o processo inicia ao definir o objeto que a pessoa quer
      imprimir, seja qual for a finalidade. Por exemplo, uma pessoa pode querer
      um chaveiro novo em formato de estrela para colocar a chave de casa, ou
      talvez essa pessoa possua uma loja de decorao e gostaria produzir um
      vaso com design bem moderno para vender.
    </p>
    <p>
      O primeiro passo  conseguir o que chamamos de "Modelo 3D" do objeto, que
      nada mais  que a verso digital, criada no computador, que contm toda
      informao da forma desse objeto. O formato de arquivo mais comum
      encontrado  o STL (stereolithography file format) que representa as
      superfcies dos objetos na forma de pequenos tringulos.
    </p>
    <p>
      Existem duas formas de se obter o modelo 3D. O primeiro,  buscando em
      alguns sites dedicados como thingiverse.com, cgtrader.com, cults3d.com
      onde  possvel buscar e fazer o download desses arquivos (alguns
      gratuitos, outros so pagos), nunca esquecendo de tomar cuidado com as
      condies na licena de uso do modelo definidas pelo autor (principalmente
      no caso de direitos de venda). A segunda forma, caso no encontre algum
      modelo que tenha gostado, ou a licena no permita o uso da maneira
      desejada,  "modelar" um novo objeto, ou seja, criar o seu prprio modelo.
      Nesse caso, habilidades de modelagem podem ser necessrias e existem
      diversos softwares (gratuitos, pagos, para PC, online...) que permitem a
      construo do objeto e a exportao em formato STL.
    </p>
    <p>
      Alguns exemplos de softwares so AutoCad (da empresa Autodesk), Blender
      (gratuito), OnShape (software de modelagem online:
      https://www.onshape.com/en/ que tem planos gratuitos e pagos dependendo do
      uso), tinkercad (software online gratuito da autodesk simples e intuitivo,
      recomendado para quem quer criar objetos simples sem precisar de muito
      conhecimento em modelagem 3D).
    </p>
    <p>
      Caso a pessoa no opte por modelar,  possvel buscar modeladores
      freelancers (inclusive pela plataforma de servios getNinjas) e pedir um
      oramento para modelar o objeto (sempre uma boa prtica avisar que o
      modelo ser utilizado para ser impresso em 3D).
    </p>

